import createHistory from 'history/createBrowserHistory';

import utils from './utils';
import { pop, unpop, slideLeft, slideRight } from './transitionConfig';

class Router {
  constructor() {
    this.history = createHistory();
    this.transitionStyles = undefined;
    this.historyKeys = [undefined];
    this.historyPaths = [window.location.pathname];
    this.idxPrev = 0;
    this.idxCurr = 0;
    this.transitionStyles = pop;
    this.history.stepBackToCloseCurrentStack = this.stepBackToCloseCurrentStack.bind(
      this
    );
    this.history.listen((location, action) => {
      const certainlyForward =
        action === 'PUSH' ||
        (action === 'POP' && this.historyKeys.indexOf(location.key) === -1);
      const maybeBack = action === 'POP';
      // console.log('location.key ACTION', location.key, action);
      if (certainlyForward) {
        this.historyKeys.push(location.key);
        this.historyPaths.push(location.pathname);
        this.idxCurr = this.historyKeys.length - 1;
        this.idxPrev = this.historyKeys.length - 2;
        if (utils.isCurrentStackStart(this.historyPaths[this.idxCurr])) {
          // console.log('pop');
          this.transitionStyles = pop;
        } else {
          // console.log('slideLeft');
          this.transitionStyles = slideLeft;
        }
      } else if (maybeBack) {
        this.idxPrev = this.idxCurr;
        this.idxCurr = location.key
          ? this.historyKeys.indexOf(location.key)
          : 0;
        if (this.idxPrev > this.idxCurr) {
          // console.log('going back');
          if (utils.isCurrentStackStart(this.historyPaths[this.idxCurr + 1])) {
            // console.log('unpop');
            this.transitionStyles = unpop;
          } else {
            // console.log('slideRight');
            this.transitionStyles = slideRight;
          }
          const numberOfRoutesToPop = this.idxPrev - this.idxCurr;
          this.historyKeys.splice(
            -1 * numberOfRoutesToPop,
            numberOfRoutesToPop
          );
          this.historyPaths.splice(
            -1 * numberOfRoutesToPop,
            numberOfRoutesToPop
          );
        }
      }
      // console.log('hstPaths', this.historyKeys, this.historyPaths);
    });
  }

  stepBackToCloseCurrentStack() {
    const currentStackStartsAt = this.historyPaths
      .slice(0)
      .reverse()
      .findIndex(i => i.indexOf(utils.sepStackFirst) !== -1);
    // console.log(
    //   'stepBackToCloseCurrentStack',
    //   this.historyPaths,
    //   currentStackStartsAt + 1
    // );
    // const stepBackToCloseCurrentStack = this.historyPaths.length - currentStackStartsAt;
    return currentStackStartsAt !== -1 ? currentStackStartsAt + 1 : 0;
  }
}

export default Router;



