import { createRenderer } from 'fela';
import embedded from 'fela-plugin-embedded';
import prefixer from 'fela-plugin-prefixer';
import fallbackValue from 'fela-plugin-fallback-value';
import unit from 'fela-plugin-unit';
// import lvha from 'fela-plugin-lvha'
// import validator from 'fela-plugin-validator'
// import logger from 'fela-plugin-logger'
// import perf from 'fela-perf'
// import beautifier from 'fela-beautifier'

export enum FontSize {
  small = 12,
  normal = 14,
  large = 16,
  // xlarge and below are artificial - assign real values to them
  xlarge = 18,
  xxlarge = 20,
  xxxlarge = 24,
}

export enum FontWeight {
  light = '300',
  normal = '400',
  medium = '500',
  bold = '600',
}

export enum Color {
  green = '#0FB82C',
}

// TODO basic helper to be fune tunes to all available font combinations
export const font = (
  fontSize: FontSize,
  fontWeight: FontWeight = FontWeight.normal
) => ({
  fontSize,
  fontWeight,
});

export const baseTransitionStyles = {
  position: 'absolute',
  // this is height of `StatusBar`, should be collapsible to 0
  top: 0,
  width: '100%',
  minWidth: 320,
  maxWidth: 414,
  margin: '0 auto',
  left: 0,
  right: 0,
  bottom: 0,
};

export const felaRenderer = () => {
  const renderer = createRenderer({
    plugins: [
      embedded(),
      prefixer(),
      fallbackValue(),
      unit(),
      // lvha(),
      // validator(),
      // logger()
    ],
    // enhancers: [perf(), beautifier()]
  });

  renderer.renderStatic(
    {
      width: '100%',
      height: '100%',
      margin: 0,
      padding: 0,
      fontFamily: 'sans-serif',
    },
    'html,body,#root'
  );

  renderer.renderStatic(
    {
      background: 'linear-gradient(#999, #ccc, #999)',
      backgroundColor: '#888',
      backgroundRepeat: 'no-repeat',
      backgroundAttachment: 'fixed'
    },
    'html'
  );

  // renderer.renderStatic({ display: 'flex' }, 'div')
  return renderer;
};



