import * as React from 'react';
import { connect } from 'react-fela';

import { font, FontSize, FontWeight, Color } from '../fela';

const Header: React.SFC<React.HTMLProps<JSX.Element>> = (
  props: React.HTMLProps<JSX.Element> & {
    title: string;
    styles: { root: string };
  }
) =>
  <h3 className={props.styles.root}>
    {props.title}
  </h3>;

Header.defaultProps = {
  title: 'default title',
};

type PropsWithColor = {
  color: string;
};

const keyframe = (props: PropsWithColor) => ({
  '0%': { color: props.color },
  '50%': { color: 'blue' },
  '80%': { color: 'purple' },
  '100%': { color: props.color },
});

const styleSheet = {
  root: (props: PropsWithColor) => ({
    animationName: keyframe(props),
    animationDuration: `2s`,
    // animationIterationCount: 'infinite',
    animationIterationCount: 0,
    color: Color.green,
    opacity: 0.2,
    ...font(FontSize.xxxlarge, FontWeight.normal),
    // padding: 50,
    ':hover': { color: props.color, animationDuration: '500ms' },
    '@media (max-width: 800px)': { fontSize: 25 },
    // textAlign: 'center',
  }),
};

export default connect(styleSheet)(Header);



