const stackStartDivider = '--';
const sepStackFirst = `/${stackStartDivider}/`;
const sepStackTail = '/-/';

const getStartOfLastStack = (url: string) => {
  return url.lastIndexOf(sepStackFirst) !== -1
    ? url.lastIndexOf(sepStackFirst)
    : url.lastIndexOf(sepStackTail);
};

const isCurrentStackStart = (url: string) => {
  const urlParts = url.split('/');
  return urlParts[urlParts.length - 2] === stackStartDivider;
};

const endsWithSlash = (url: string) => url.slice(-1, 1) === '/';

const removeTrailingSlash = (url: string) =>
  endsWithSlash(url) ? url.slice(0, url.length - 1) : url;

const makeUrl = (
  currentUrl: string,
  targetUrl: string,
  isNewStack: boolean
) => {
  if (isNewStack) {
    return removeTrailingSlash(currentUrl) + sepStackFirst + targetUrl;
  }
  return (
    currentUrl.substring(0, getStartOfLastStack(currentUrl)) +
    sepStackTail +
    targetUrl
  );
};

export default {
  sepStackFirst,
  sepStackTail,
  getStartOfLastStack,
  isCurrentStackStart,
  makeUrl
};



