import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Router, Route, Switch } from 'react-router-dom';

import StatusBar from './components/StatusBar';
import BMRouter from './routes/navigation/Router';
import PageWrapper from './routes/navigation/PageWrapper';
import RouteTransition from './routes/navigation/RouteTransition';
import ScrollToTop from './routes/navigation/ScrollToTop';
import routes from './routes';

const rts = Object.keys(routes).map(key => {
  return (
    <Route
      exact
      path={routes[key].root ? key : `/:s*/${key}`}
      component={routes[key].component}
      key={key}
    />
  );
});

const routeStyleInTransition = {
  // backgroundColor: 'yellow',
  height: '100%',
  overflowX: 'hidden',
  // overflowY: 'scroll',
  position: 'absolute',
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
};

const routeStyleStatic = {
  // backgroundColor: 'green',
  position: 'relative',
  height: '100%',
};

const runOnMount = false;

class App extends Component {
  constructor(props) {
    super(props);
    this.router = new BMRouter();
    this.renderRoutes = this.renderRoutes.bind(this);
    this.state = {
      inTransition: runOnMount,
      headerPinned: true,
    };
  }

  static childContextTypes = {
    headerPinned: PropTypes.bool,
  }

  getChildContext() {
    return {
      headerPinned: this.state.headerPinned,
    };
  }

  startTransition = () => {
    this.setState({
      inTransition: true
    });
  }

  endTransition = () => {
    this.setState({
      inTransition: false
    });
  }

  onHeaderPin = () => {
    console.log('onPin');
    this.setState({
      headerPinned: true,
    })
  }

  onHeaderUnpin = () => {
    console.log('onUnpin');
    this.setState({
      headerPinned: false,
    })
  }

// overflowX: 'overlay', position: 'absolute', top: 0, bottom: 0, left: 0, right: 0
  renderRoutes({ location, history, match }) {
    return (
      <div style={{ height: '100%' }}>
        <StatusBar onPin={this.onHeaderPin} onUnpin={this.onHeaderUnpin} onUnfix={this.onHeaderUnpin} />
        <PageWrapper>
          <RouteTransition
            pathname={location.key || 'initRoute'}
            {...this.router.transitionStyles}
            runOnMount={false}
            onWillEnter={this.startTransition}
            onDidLeave={this.endTransition}
            style={this.state.inTransition
              ? routeStyleInTransition
               : routeStyleStatic
             }
          >
              <Switch key={location.key} location={location}>
                {rts}
              </Switch>
            </RouteTransition>
        </PageWrapper>
      </div>
    );
  }

  render() {
    return (
      <Router history={this.router.history}>
        <ScrollToTop>
          <Route render={this.renderRoutes} />
        </ScrollToTop>
      </Router>
    );
  }
}

export default App;



