import * as React from 'react';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import { RouteComponentProps } from 'react-router';

import utils from './utils';

export interface BMLinkProps extends RouteComponentProps<any> {
  newStack: boolean,
  to: string,
  children: any,
};

export default withRouter(
  class BMLink extends React.Component<BMLinkProps, undefined> {

    render() {
      const { newStack, to, location, children } = this.props;
      return (
        <RouterLink to={utils.makeUrl(location.pathname, to, newStack)}>
          {children}
        </RouterLink>
      );
    }

  }

)



