import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect as connectFela } from 'react-fela';
import { StickyContainer, Sticky } from 'react-sticky';
// import Sticky from 'react-stickynode');

import Header from '../../components/Header';
import { Footer } from '../../components/Footer';

class Page extends Component {

  static contextTypes = {
    headerPinned: PropTypes.bool,
  }

  closeStack = () => {
    this.props.history.go(
      -1 * this.props.history.stepBackToCloseCurrentStack()
    );
  }

  render() {
    const { children, title, styles } = this.props;
    console.log('--------this.props', this.context.headerPinned, this.props);
    const stepBackToCloseCurrentStack = this.props.history.stepBackToCloseCurrentStack();
    return (
      <StickyContainer>
        {/* ------- todo !!!!!!!! HEADER HEIGHT  50 */}
        <Sticky className={styles.root}>
          {({ style,

              // the following are also available but unused in this example
              isSticky,
              wasSticky,
              distanceFromTop,
              distanceFromBottom,
              calculatedHeight }) => {
            console.log('style, isSticky, wasSticky, distanceFromTop, distanceFromBottom, calculatedHeight', style, isSticky, wasSticky, distanceFromTop, distanceFromBottom, calculatedHeight);
            if (this.context.headerPinned) {
              style.marginTop = '50';
              style.transition = 'margin 0.25s ease-in-out';
            }
            console.log('style', style);
            return (
              <div className={styles.header} style={style}>
                {stepBackToCloseCurrentStack > 1 &&
                  <div className={styles.goBack} onClick={this.props.history.goBack}>
                    ←
                  </div>}
                <Header color='yellow' title={title} />
                {stepBackToCloseCurrentStack > 0 &&
                  <div className={styles.goBack} onClick={this.closeStack}>
                    ✕
                  </div>}
              </div>
            )
          }}
        </Sticky>
        <div className={styles.content}>
          {children}
          <Footer title='footer TS component' />
        </div>
      </StickyContainer>
    );
  }

}

const styleSheet = {
  root: () => ({
    // position: 'absolute',
    // top: 0,
    // left: 0,
    // bottom: 0,
    // right: 0,
    backgroundColor: 'red',
  }),
  header: (props) => ({
    backgroundColor: 'red',
    display: 'flex',
    justifyContent: 'space-between',
    borderBottomWidth: 1,
    borderBottomColor: '#555',
    borderBottomStyle: 'solid',
  }),
  goBack: () => ({
    userSelect: 'none',
    cursor: 'pointer',
    fontSize: 25,
    padding: 16,
    color: 'rgba(0, 0, 0, 0.3)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-around',
  }),
  content: () => ({
    backgroundColor: '#fff',
  }),
}

styleSheet.closeStack = styleSheet.goBack;

export default withRouter(connectFela(styleSheet)(Page));



