import spring from 'react-motion/lib/spring';

import { baseTransitionStyles } from '../../fela';

const fadeConfig = { stiffness: 100, damping: 22 };
const popConfig = { stiffness: 100, damping: 18 };
const slideConfig = { stiffness: 100, damping: 18 };

// const fade = {
//   atEnter: {
//     opacity: 0
//   },
//   atLeave: {
//     opacity: spring(0, fadeConfig)
//   },
//   atActive: {
//     opacity: spring(1, fadeConfig)
//   }
// };

const mapPopUnpopStyles = styles => ({
  ...baseTransitionStyles,
  opacity: styles.opacity,
  // transform: `translate3d(0, ${styles.offset}%, 0)`,
});

const mapSlideStyles = styles => ({
  ...baseTransitionStyles,
  opacity: styles.opacity,
  transform: `translate3d(${styles.offset}%, 0, 0)`
})

export const pop = {
  atEnter: {
    opacity: 0,
    offset: 100
  },
  atLeave: {
    opacity: spring(0, fadeConfig),
    offset: spring(0, popConfig)
  },
  atActive: {
    opacity: spring(1, popConfig),
    offset: spring(0, popConfig)
  },
  mapStyles: mapPopUnpopStyles
};

export const unpop = {
  atEnter: {
    opacity: 0,
    offset: 0
  },
  atLeave: {
    opacity: spring(0, fadeConfig),
    offset: spring(100, popConfig)
  },
  atActive: {
    opacity: spring(1, popConfig),
    offset: spring(0, popConfig)
  },
  mapStyles: mapPopUnpopStyles
};

export const slideLeft = {
  atEnter: {
    opacity: 0,
    offset: 100
  },
  atLeave: {
    opacity: spring(0, fadeConfig),
    offset: spring(-100, slideConfig)
  },
  atActive: {
    opacity: spring(1, slideConfig),
    offset: spring(0, slideConfig)
  },
  mapStyles: mapSlideStyles
};

export const slideRight = {
  atEnter: {
    opacity: 0,
    offset: -100
  },
  atLeave: {
    opacity: spring(0, fadeConfig),
    offset: spring(100, slideConfig)
  },
  atActive: {
    opacity: spring(1, slideConfig),
    offset: spring(0, slideConfig)
  },
  mapStyles: mapSlideStyles
};



